  }

  .desc {
    opacity: 0.7;
  }

  #custom-cursor {
    display: none;
  }

  .viewCv {
    width: 50%;
  }

  .eu {
    width: 80%;
  }

  .conteudo {
    width: 85%;
  }

  .socials {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 40%;
    margin-top: 5%;
  }

  .desc {
    display: none;
  }

  #grid {
    display: none;
  }

  #gridPrincipal {
    width: 100%
  }

  .containerPrincipal {
    margin-top: 0;
    width: 100%;
    align-items: center;
    flex-direction: column;
    height: 100%;
    justify-content: center;
  }

  .eu h1 {
    font-size: xx-large;
  }

  .desc {
    padding-top: 15%;
